}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.shop {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
  width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .shop {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .shop {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .shop {
    grid-template-columns: repeat(4, 1fr);
  }
}

