.stars {
  display: flex;
  margin: 0;

  &__star {
    background-image: url(../images/star.svg);
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  @for $star from 1 through 5 {
    &--#{$star} {
      .stars__star:nth-child(-n+#{$star}) {
        background-image: url(../images/star-active.svg);
      }
    }
  }
}
